<template>
	<view class="page_article" id="article_details">
		<div_article style="background-color: #fff;" :obj="obj" class="mb"></div_article>
		<!-- 推荐文章 -->
		<div class="recommend" style="background-color: #fff;">
			<bar_title title="喜欢此内容的人还喜欢"></bar_title>
			<list_article :list="list_article"></list_article>
		</div>
		<!-- /推荐文章 -->

		<!-- 文章评论列表 -->
		<bar_title v-if="$check_action('/comment/list','get')" title="文章点评"></bar_title>
		<list_comment v-if="$check_action('/comment/list','get')"  style="background-color: #fff;" :list="list_comment"></list_comment>
		<!-- /文章评论列表 -->
		<!-- 发表评论 -->
		<view class="pa" v-if="$check_action('/comment/list','add')">
			<navigator class="link" :url="'/pages/comment/edit?source_table=article&source_field=article_id&source_id=' + obj.article_id">我来评论</navigator>
		</view>
		<!-- {{ obj }} -->
		<!-- {{ form }} -->
	</view>
</template>

<script>
	import bar_title from "../../components/diy/bar_title.vue";
	import list_article from "../../components/diy/list_article.vue";
	import div_article from "../../components/diy/div_article.vue";
	import list_comment from "../../components/diy/list_comment.vue";

	import mixin from "../../mixins/page.js";

	export default {
		mixins: [mixin],
		components: {
			bar_title,
			list_article,
			div_article,
			list_comment
		},
		data() {
			return {
				url_get_obj: "~/api/article/get_obj?",
				field: "article_id",
				query: {
					article_id: 0
				},
				obj: {},
				list_article: [],
				list_comment: [],
				form: {
					content: ""
				}
			}
		},
		methods: {
			onEditorReady() {
				const that = this
				uni.createSelectorQuery().select('#editor').context((res) => {

					this.editorCtx = res.context
					this.editorCtx.setContents({
						html: this.form.content,
						success: (res) => {
							console.log(res)
						},
						fail: (res) => {
							console.log(res)
						},
					})
				}).exec()
			},
			// 获取文章
			get_article() {
				this.$get("~/api/article/get_list?", {
					"page": 1,
					"size": 2
				}, (json) => {
					if (json.result) {
						var list_article = json.result.list
						this.list_article = list_article
					}
				})
			},
			// 获取评论
			get_comment() {
				var options = getCurrentPages()[getCurrentPages().length - 1].options
				var query = {
					"source_table":"article",
					"source_field":"article_id",
					"source_id":options.article_id
				}
				this.$get("~/api/comment/get_list?", query, (json) => {
					if (json.result) {
						var list_comment = json.result.list
						this.list_comment = list_comment
					}
				})
			},
			editorChange: function(e) {
				this.gongsijieshao = e.detail.html
			},
			/**
			 * 获取对象之后
			 * @param {Object} json 结果对象
			 */
			get_obj_after(json) {
				this.add_hits(json.result.obj)
			},
			/**
			 * 添加访问量
			 */
			add_hits(obj) {
				console.log("-------------------");
				this.$post('~/api/article/set?article_id=' + obj.article_id, {
					hits: obj.hits + 1
				}, res => {
					obj.hits += 1
					console.log(res);
				})
			}
		},
		onLoad() {
			this.get_article()
			this.get_comment()
		}
	}
</script>

<style scoped>
	.page_article {
		padding-bottom: 1rem;
	}

	.bar_title {
		background: none;
	}

	.recommend {
		margin: 0.5rem;
		border-radius: 0.5rem;
		border: 1px solid #dbdbdb;
		overflow: hidden;
	}

	.link {
		text-align: center;
		padding: 0.5rem 0;
		border: 1px solid #DBDBDB;
		border-radius: 0.5rem;
	}
</style>
